function getList (params) {
  return new Promise((resolve, reject) => {
    let data = [
      { name: '1', id: 1 },
      { name: '2', id: 2 },
      { name: '3', id: 3 },
      { name: '4', id: 4 },
      { name: '5', id: 5 },
      { name: '6', id: 6 },
      { name: '7', id: 7 },
      { name: '8', id: 8 },
      { name: '9', id: 9 },
      { name: '10', id: 10 }
    ]
    if (params.pageNo > 1) {
      data = data.map(item => {
        return {
          name: item.name + params.pageNo,
          id: item.id + 'a' + params.pageNo
        }
      })
    }
    setTimeout(() => {
      let results = {
        rows: params.label ? data.filter(item => item.name.includes(params.label)) : data,
        total: 40
      }
      resolve(results)
    }, 400)
  })
}
export default { getList }
